*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

.navbar{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color:hsl(0, 0%, 98%);
}

.navWrapper {
   height: calc(3rem) ;
   display: flex;
   justify-content: space-between;
   align-items: center;
   column-gap: 1rem;
   width: 100%;
   max-width: 968px;
   margin-left: auto;
   margin-right: auto;
}

.navLogo,
.navToggle {
    color:  #393739;
    font-weight: 500;
}

.navList{
    display: flex;
    gap: 0 2rem;
    list-style: none;
}


.navclose , .navToggle , .navIcons {
display: none;
}



.navLinks{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: #393739;
    font-size: 0.875rem;
    font-weight: 500;
    transition: .3s;
}

.uli{
    color: #393739;
  
}



.navLinks:hover {
    color:  #7FD1AE;
}

@media screen and (max-width: 992px) {
    .navWrapper {
       margin: 0px auto 0 auto;
       padding-left: 20px ;
       padding-right: 20px;
    }
 

  }


  @media screen and (max-width: 768px) {
     .navbar{
        top: initial;
        bottom: 0;
     }

     .navWrapper {
        height: 3rem + 1.5rem;
     }

     .navMenu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: hsl(0, 0%, 98%);
        padding: 2rem 1.5rem 4rem  1.5rem; 
        border-radius: 1.5rem 1.5rem 0 0;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        transition: 3s;
     }
     
     .showmenu{
        bottom: 0;
   
     }

     .navList {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 2rem;
     }
     

     .navLinks {
        font-size: 1.4rem;
     }

     .navclose{
        position: absolute;
        right: 1.5rem;
        top: 0.8rem;
        font-size: 1.5rem;
        color: #7FD1AE;
        cursor: pointer;
     }

     /* .navclose:hover{
        color: #393739;
     } */


     .navclose , .navToggle , .navIcons {
        display: block;
        }

        .navToggle{
            cursor: pointer;
            font-size: 1.1rem;
         }
        
  }

  @media screen and (max-width: 380px) {
    .navMenu {
        padding: 2rem 0.25rem 4rem 0.25rem;
    }


    .navclose{
        top: 0.5rem;
        right: 1rem;
     }

     .navList{
        gap: 2rem 0;
     }

     .navToggle{
        cursor: pointer;
        font-size: 1.1rem;
     }
  }
  









